<template>
  <v-app-bar
    id="core-app-bar"
    v-model="inputValue"
    absolute
    app
    color="transparent"
    flat
    height="88"
  >
    <v-toolbar-title class="primary--text font-weight-light align-self-center">
      <v-btn
        icon
        color="primary"
        @click.stop="onClick"
      >
        <v-icon>mdi-view-list</v-icon>
      </v-btn>
      {{ $route.name }}
    </v-toolbar-title>

    <v-spacer />

    <v-toolbar-items>
      <v-row
        align="center"
        justify="end"
        class="mx-0"
      >
        <v-col
          v-if="sites && sites.length > 0"
          cols="7"
        >
          <v-select
            v-model="siteName"
            label="Site"
            solo
            dense
            hide-details
            :items="sites.map(site => site.name)"
            @change="() => setSite(siteName)"
          />
        </v-col>
        <v-menu
          bottom
          left
          offset-y
          transition="slide-y-transition"
        >
          <template v-slot:activator="{ attrs, on }">
            <v-btn
              class="toolbar-items"
              icon
              :to="user ? null : '/login?redirect=' + $route.path"
              v-bind="attrs"
              v-on="on"
            >
              <v-icon color="primary">
                mdi-account
              </v-icon>
            </v-btn>
          </template>

          <v-card v-if="user">
            <v-list
              color="transparent"
              dense
            >
              <v-list-item
                to="/profile"
              >
                <avatar
                  :image="user.image"
                  :name="user.name"
                  list
                />
                <v-list-item-content>
                  <v-list-item-title>
                    {{ user.name }}
                    <v-chip
                      v-if="user.role && user.role.toLowerCase() !== 'default'"
                      color="tertiary"
                      x-small
                      class="mx-1 px-2"
                    >
                      {{ capitalize(user.role) }}
                    </v-chip>
                  </v-list-item-title>
                  <v-list-item-subtitle>{{ user.company }}</v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list>
            <div class="text-right pa-2">
              <v-btn
                v-if="user"
                to="/profile"
                class="mr-2"
                text
              >
                Profile
              </v-btn>
              <v-btn
                v-if="user"
                class="primary"
                @click="logout"
              >
                Log out
              </v-btn>
            </div>
          </v-card>
        </v-menu>
      </v-row>
    </v-toolbar-items>
  </v-app-bar>
</template>

<script>
  // Utilities
  import {
    mapMutations,
  } from 'vuex'

  export default {
    data: () => ({
      siteName: null,
      responsive: false,
    }),
    computed: {
      user: function () {
        return this.$store.state.user
      },
      sites: function () {
        return this.$store.state.sites
      },
      inputValue: {
        get () {
          return this.$store.state.app.bar
        },
      }
    },
    created () {
      if (this.user) {
        this.getCompanyAndSites()
      }
    },
    mounted () {
      this.onResponsiveInverted()
      window.addEventListener('resize', this.onResponsiveInverted)
    },
    beforeDestroy () {
      window.removeEventListener('resize', this.onResponsiveInverted)
    },

    methods: {
      ...mapMutations('app', ['setDrawer', 'toggleDrawer', 'setBar']),
      onClick () {
        this.setDrawer(!this.$store.state.app.drawer)
      },
      setSite (name) {
        var siteId = this.sites.find(site => site.name === name).siteId
        this.$store.commit('setSite', siteId)
      },
      onResponsiveInverted () {
        if (window.innerWidth < 991) {
          this.responsive = true
        } else {
          this.responsive = false
        }
      },
      logout () {
        this.$router.push('/login?logout=1')
      },
      getCompanyAndSites () {
        this.api.get('company')
          .then(result => {
            this.$store.commit('setCompany', result.data)
          }, error => {
            this.error = error
          })
        this.$emit('setLoadingSites', true)
        this.api.get('sites')
          .then(result => {
            this.$emit('setLoadingSites', false)
            this.$store.commit('setSites', result.data)
            if (result.data && result.data.length > 0) {
              var selectedSite = result.data[0].siteId
              this.$store.commit('setSite', selectedSite)

              if (!this.siteName) {
                this.siteName = result.data.find(site => site.siteId === selectedSite).name
              }
            }
          }, error => {
            this.$emit('setLoadingSites', false)
            this.error = error
          })
      }
    }
  }
</script>

<style>
  /* Fix coming in v2.0.8 */
  #core-app-bar {
    width: auto;
  }

  #core-app-bar a {
    text-decoration: none;
  }
</style>
